// @ts-ignore
import React, { useEffect, useState } from "react";
import { neos } from "@neos-project/neos-ui-decorators";
import { HexColorPicker, HexColorInput } from "react-colorful";
import { onHexChange, setLuminance, setLightness, setStateFromValue, OptionPreview, RangeSlider } from "./Components";
import { IconButton, SelectBox } from "@neos-project/react-ui-components";
import * as stylex from "@stylexjs/stylex";
import "./Tokens.stylex";
import { colors, sizes, fonts, transitions } from "./Tokens.stylex";
const neosifier = neos(globalRegistry => ({
  i18nRegistry: globalRegistry.get("i18n"),
  config: globalRegistry.get("frontendConfiguration").get("Carbon.ColorPicker.OKLCH")
}));
const defaultProps = {
  options: {
    mode: "all",
    disabled: false,
    allowEmpty: true,
    showPresets: true,
    showPicker: true,
    showHexInput: true,
    showLightness: false,
    showLuminance: false,
    precision: 5,
    presets: {}
  }
};
const styles = {
  disabled: {
    cursor: "x1h6gzvc",
    opacity: "x190dgpg",
    ":where(*) *_pointerEvents": "x1g8rrie",
    $$css: true
  },
  highlight: {
    borderRadius: "xjohyra",
    borderStartStartRadius: null,
    borderStartEndRadius: null,
    borderEndStartRadius: null,
    borderEndEndRadius: null,
    borderTopLeftRadius: null,
    borderTopRightRadius: null,
    borderBottomLeftRadius: null,
    borderBottomRightRadius: null,
    boxShadow: "x11mstge",
    $$css: true
  },
  wrapper: {
    display: "x78zum5",
    flexDirection: "xdt5ytf",
    gap: "x188dswm",
    $$css: true
  },
  colorPreview: {
    borderRadius: "xjohyra",
    height: "x1hhc0k",
    minWidth: "x15g7okx",
    flex: "x98rzlu",
    $$css: true
  },
  colorPreviewTransparent: {
    backgroundSize: "x1ht9rhz",
    backgroundColor: "x1qjns4s",
    backgroundImage: "x1149thc",
    $$css: true
  }
};

// @ts-ignore
function Editor(props) {
  const options = {
    ...defaultProps.options,
    ...props.config,
    ...props.options
  };
  const {
    value,
    commit,
    highlight,
    i18nRegistry
  } = props;
  const {
    allowEmpty,
    disabled,
    mode,
    precision,
    presets,
    showHexInput,
    showLightness,
    showLuminance,
    showPicker,
    showPresets
  } = options;
  if (mode !== "coords" && mode !== "hex" && mode !== "all" && mode !== "oklch") {
    return <div {...{
      className: "xjohyra x1hgx17m x89w75u"
    }}>
                {i18nRegistry.translate("Carbon.ColorPicker.OKLCH:Main:invalidMode", "", [mode])}
            </div>;
  }
  const [state, setState] = useState(setStateFromValue(value, mode, precision));
  useEffect(() => {
    if (!state?.hex) {
      if (value == "") {
        return;
      }

      // Make no commit if the color is empty
      if ((mode === "all" || mode === "coords") && (JSON.stringify(value) == "{}" || JSON.stringify(value) == "[]")) {
        return;
      }
      commit("");
      return;
    }
    if (mode === "all") {
      if (value.oklch != state.oklch) {
        commit({
          hex: state.hex,
          oklch: state.oklch,
          coords: state.coords
        });
      }
      return;
    }
    if (mode === "coords") {
      if (JSON.stringify(value) != JSON.stringify(state.coords)) {
        commit(state.coords);
      }
      return;
    }
    if (value != state[mode]) {
      commit(state[mode]);
    }
  }, [state]);
  function handleHexChange(hex: string) {
    setState(onHexChange(hex, precision));
  }
  function handleLightnessChange(lightness: number) {
    handleHexChange(setLightness(state?.hex, lightness));
  }
  function handleLuminanceChange(luminance: number) {
    handleHexChange(setLuminance(state?.oklch, luminance));
  }
  const presetOptions = showPresets && presets && Object.entries(presets).map(([key, color]) => ({
    value: color,
    label: key
  })).filter(preset => !!preset.value);
  const lightness = state?.lightness || 0;
  const luminance = state?.coords?.l || 0;
  return <div {...stylex.props(styles.wrapper, disabled && styles.disabled)}>
            {Boolean(showPicker) && <HexColorPicker {...{
      className: "xh8yej3 xt7dq6l x1plog1 xlt0ddm xf5yl2k x9av0op xdosjj7 xkmff00 xbq8i1p x19mnrqj x13bp1ud x1th5coa x1lptuiq x4hip2o xfbmp4j x2lqnu4 xmxw2tw x46gmau"
    }} color={state?.hex} onChange={handleHexChange} />}

            {Boolean(showLightness) && <RangeSlider disabled={state?.hex ? false : true} value={lightness} onChange={handleLightnessChange} label={i18nRegistry.translate("Carbon.ColorPicker.OKLCH:Main:lightness")} />}
            {Boolean(showLuminance) && <RangeSlider disabled={state?.coords?.l ? false : true} value={luminance * 100} onChange={handleLuminanceChange} label={i18nRegistry.translate("Carbon.ColorPicker.OKLCH:Main:luminance")} />}

            <div {...{
      className: "x78zum5 x188dswm"
    }}>
                <div {...stylex.props(styles.colorPreview, state?.oklch || styles.colorPreviewTransparent, highlight && styles.highlight)} style={{
        backgroundColor: state?.oklch
      }} />
                {Boolean(showHexInput) && <HexColorInput {...{
        className: "x1iyjqo2 x1hhc0k xh8yej3 xjohyra x1wty727 x1qjns4s xtqyqw7 x1awj2ng x1a2a7pz x1agkqgv xtw1abo x1sirlc8"
      }} title={i18nRegistry.translate("Carbon.ColorPicker.OKLCH:Main:setHexColor")} color={state?.hex} onChange={handleHexChange} prefixed={false} />}
                {Boolean(allowEmpty) && <IconButton style="lighter" icon="times" title={i18nRegistry.translate("Carbon.ColorPicker.OKLCH:Main:resetColor")} onClick={() => {
        setState(null);
      }} />}
            </div>
            {Boolean(!!presetOptions) && <SelectBox options={presetOptions} value={state?.hex} placeholder={i18nRegistry.translate("Carbon.ColorPicker.OKLCH:Main:preset")} allowEmpty={false} onValueChange={handleHexChange} ListPreviewElement={OptionPreview} />}
        </div>;
}
export default neosifier(Editor);